  const handleLike = async (e) => {
    if (e) e.stopPropagation();
    
    const wasLiked = liked;
    const prevCount = likesCount;
    
    try {
      setLiked(!wasLiked);
      setLikesCount(wasLiked ? Math.max(0, prevCount - 1) : prevCount + 1);
      
      const response = await axios.post(`/api/posts/${post.id}/like`);
      if (response.data) {
        setLiked(response.data.liked);
        setLikesCount(response.data.likes_count);
      }
    } catch (error) {
      console.error('Error liking post:', error);
      setLiked(wasLiked);
      setLikesCount(prevCount);
      
      if (error.response && error.response.status === 429) {
        const rateLimit = error.response.data.rate_limit;
        let errorMessage = error.response.data.error || "Ð¡Ð»Ð¸ÑˆÐºÐ¾Ð¼ Ð¼Ð½Ð¾Ð³Ð¾ Ð»Ð°Ð¹ÐºÐ¾Ð². ";
        
        if (rateLimit && rateLimit.reset) {
          const resetTime = new Date(rateLimit.reset * 1000);
          const now = new Date();
          const diffSeconds = Math.round((resetTime - now) / 1000);
          
          if (!errorMessage.includes("Ð¿Ð¾Ð´Ð¾Ð¶Ð´Ð¸Ñ‚Ðµ")) {
            if (diffSeconds > 60) {
              const minutes = Math.floor(diffSeconds / 60);
              const seconds = diffSeconds % 60;
              errorMessage += ` ÐŸÐ¾Ð¶Ð°Ð»ÑƒÐ¹ÑÑ‚Ð°, Ð¿Ð¾Ð´Ð¾Ð¶Ð´Ð¸Ñ‚Ðµ ${minutes} Ð¼Ð¸Ð½. ${seconds} ÑÐµÐº.`;
            } else {
              errorMessage += ` ÐŸÐ¾Ð¶Ð°Ð»ÑƒÐ¹ÑÑ‚Ð°, Ð¿Ð¾Ð´Ð¾Ð¶Ð´Ð¸Ñ‚Ðµ ${diffSeconds} ÑÐµÐº.`;
            }
          }
        }
        
        
        window.dispatchEvent(new CustomEvent('rate-limit-error', { 
          detail: { 
            message: errorMessage,
            shortMessage: "Ð›Ð¸Ð¼Ð¸Ñ‚ Ð»Ð°Ð¹ÐºÐ¾Ð²",
            notificationType: "warning",
            animationType: "bounce", 
            retryAfter: rateLimit?.reset ? Math.round((new Date(rateLimit.reset * 1000) - new Date()) / 1000) : 60
          } 
        }));
      }
    }
  };

