import { useState, useCallback } from 'react';

/**
 * Ð¥ÑƒÐº Ð´Ð»Ñ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸ÐµÐ¼ ÐºÐ¾Ð½Ñ‚ÐµÐºÑÑ‚Ð½Ð¾Ð³Ð¾ Ð¼ÐµÐ½ÑŽ
 * @returns {Object} - Ð¡Ð¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð¸ Ð¼ÐµÑ‚Ð¾Ð´Ñ‹ ÐºÐ¾Ð½Ñ‚ÐµÐºÑÑ‚Ð½Ð¾Ð³Ð¾ Ð¼ÐµÐ½ÑŽ
 */
const useContextMenu = () => {
  const [contextMenuState, setContextMenuState] = useState({
    show: false,
    x: 0,
    y: 0,
    data: null
  });

    const handleContextMenu = useCallback((e, data = null) => {
    e.preventDefault();     
    setContextMenuState({
      show: true,
      x: e.clientX,
      y: e.clientY,
      data
    });
  }, []);

    const closeContextMenu = useCallback(() => {
    setContextMenuState(prev => ({
      ...prev,
      show: false
    }));
  }, []);

  return {
    contextMenuState,
    handleContextMenu,
    closeContextMenu
  };
};

export default useContextMenu; 